<template>
  <div class="order-item-wrap">
    <div class="clearfix order-header">
      <div class="f-l shop-title">
        <i class="fa fa-cart-plus" aria-hidden="true"></i>
        <span>重卡汇商城</span>
        <i class="fa fa-chevron-right" aria-hidden="true"></i>
      </div>
      <div class="f-r state">
        <p class="state-success">{{orderInfo.type}}</p>
      </div>
    </div>
    <OrderGoods
      v-for="(item, index) in orderInfo.detail"
      :key="index"
      :goods="item"
    ></OrderGoods>
    <div class="info">
      <div class="pay-price">
        <div class="cont">
          <span v-show="orderInfo.refund_apply && !orderInfo.refund_fee" style="color:red;">您的退款申请正在处理中</span>
          <span v-show="orderInfo.refund_content" style="color:red;">{{orderInfo.refund_content}}</span>
          <span v-show="orderInfo.refund_apply && orderInfo.refund_fee" style="color:#999;">退款完成</span>
          <span>合计：<b>{{orderInfo.total_fee}}</b></span>
        </div>
      </div>
    </div>
    <div class="order-btn">
      <div class="clearfix btns">
        <a href="javascript:void(0)" @click="deleteOrder">删除订单</a>
        <a href="javascript:void(0)" @click="pay" v-show="orderInfo.unpaid">立即支付</a>
        <a href="javascript:void(0)" @click="refundsClick" v-show="orderInfo.pay && orderInfo.refund">申请退款</a>
      </div>
    </div>
  </div>
</template>

<script>
import OrderGoods from './order-goods.vue'
import bus from '../../../assets/js/bus'
export default {
  name: 'Order-Item',
  props: {
    orderInfo: Object
  },
  methods: {
    deleteOrder () {
      bus.$emit('removeOneOrder', this.orderInfo)
    },
    pay () {
      bus.$emit('clickPayBtn', this.orderInfo)
    },
    refundsClick () {
      this.$router.replace('/mall/aftersales/' + this.orderInfo.order_no)
    }
  },
  components: {
    OrderGoods
  }
}
</script>

<style lang="less" scoped>
  .order-item-wrap{
    position: relative;
    margin-bottom: .213rem;
    border-bottom: 1px solid #e7e7e7;
    padding: 0 .32rem;
    background: #fff;
    .order-header{
      padding: .33rem 0;
      .shop-title{
        height:1.067rem;
        padding: 0 .32rem;
        font-size: 12px;
        color: #777;
        box-sizing: border-box;
        background-color: #fff;
        i {
          color: #777;
        }
        span{
          color: #777;
        }
      }
      .state{
        margin-right: .32rem;
        text-align: right;
        font-size: 12px;
        p{
          margin: 0;
        }
        /*.state-success:after{*/
          /*content: "交易成功";*/
          /*color: forestgreen;*/
        /*}*/
        /*.state-error:after{*/
          /*content: "交易失败";*/
          /*color: #ff5000;*/
        /*}*/
      }
    }
    .info{
      .pay-price{
        min-height: .8rem;
        padding: .267rem .32rem .267rem 0;
        text-align: right;
        box-pack: center;
        background: #fff;
        border-bottom: 1px solid #e7e7e7;
        .cont{
          width: 100%;
          word-break: break-all;
          span{
            display: inline-block;
            margin-left: .32rem;
            b{
              font-weight: 700
            }
          }
        }
      }
    }
    .order-btn{
      .btns{
        padding: .267rem .32rem .267rem 0;
        border-bottom: 1px solid #e7e7e7;
        background: #fff;
        a{
          float: right;
          padding: 0px 1.267rem;
          margin: .133rem 0 .133rem .133rem;
          height: 1.693rem;
          line-height: 1.693rem;
          text-align: center;
          border: 1px solid #999;
          border-radius: .053rem;
          font-size: 12px;
          color: #444;
        }
      }
    }
  }
</style>



