<template>
  <div class="goods-wrap">
    <div class="img">
      <img :src="goods.imgurl || '/static/img/default-bg.jpg'" alt="">
    </div>
    <div class="infos">
      <h3>{{goods.name}}</h3>
    </div>
    <div class="price">
      <p class="price-num">¥{{goods.price}}</p>
      <p class="count">x{{goods.amount}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Order-Goods',
  props: {
    goods: Object
  }
}
</script>

<style lang="less" scoped>
  .goods-wrap{
    display: flex;
    padding: .133rem .32rem;
    box-sizing: border-box;
    background-color: #f5f5f5;
    div.img{
      width: 4.6rem;
      height: 4.6rem;
      margin-right: .293rem;
      img{
        max-width: 100%;
      }
    }
    div.infos{
      flex:1;
      h3{
        font-size: 12px;
        font-weight: 400;
      }
    }
    div.price {
      font-size: 13px;
      width: 5.973rem;
      text-align: right;
      .price-num{
        font-weight: 700;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
      }
      .count{
        font-size: 12px;
        color: #999;
      }
    }
  }
</style>



