<template>
  <div class="ui-wrap m-wrap">
    <AppHeader title="我的订单" showBack="true"></AppHeader>
    <div class="order-wrap">
      <mt-navbar v-model="selected">
        <mt-tab-item id="all">全部</mt-tab-item>
        <mt-tab-item id="unpay">未支付</mt-tab-item>
        <mt-tab-item id="waitSend">待发货</mt-tab-item>
        <mt-tab-item id="waitReceive">待收货</mt-tab-item>
        <mt-tab-item id="waitCommit">待评价</mt-tab-item>
      </mt-navbar>
      <!-- tab-container -->
      <mt-tab-container v-model="selected">
        <mt-tab-container-item id="all">
          <OrderContent
            :order="orderList"
          ></OrderContent>
        </mt-tab-container-item>
        <mt-tab-container-item id="unpay">
          <OrderContent></OrderContent>
        </mt-tab-container-item>
        <mt-tab-container-item id="waitSend">
          <OrderContent></OrderContent>
        </mt-tab-container-item>
        <mt-tab-container-item id="waitReceive">
          <OrderContent></OrderContent>
        </mt-tab-container-item>
        <mt-tab-container-item id="waitCommit">
          <OrderContent></OrderContent>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
    <mt-actionsheet
      :actions="actions"
      v-model="sheetVisible">
    </mt-actionsheet>
  </div>
</template>

<script>
  /* eslint-disable */
import AppHeader from '../../../components/header/header.vue'
import OrderContent from './order-content.vue'
import {getOrderList, removeOrder} from '../../../api/home'
import {tenPayInWechat} from '../../../api/mall'
import bus from '../../../assets/js/bus'
import { Toast } from 'mint-ui'
import {initWechatSDK} from '../../../assets/js/wechat'
import {isWechat} from '../../../lib/util'
export default {
  name: 'Order',
  data () {
    return {
      selected: 'all',
      orderList: [],
      actions: [],
      sheetVisible: false
    }
  },
  created () {
    this.getDatas()
    if (isWechat()) {
      let urlArr = window.location.href.split('=')
      if (!urlArr[1]) {
        window.location.href = window.location.href + '?refresh=true'
      }
      initWechatSDK('http://m.truckgogo.com/order')
    }
  },
  mounted () {
    // 点击删除按钮
    bus.$on('removeOneOrder', order => {
      removeOrder(order.order_no)
        .then(data => {
          if (data.state) {
            Toast(data.errormsg)
            this.getDatas()
          } else {
            Toast(data.errormsg)
          }
        })
    })
    // 点击支付按钮
    bus.$on('clickPayBtn', order => {
      this.sheetVisible = true
      let self = this
      this.$set(this, 'actions', [
        {
          name: '微信支付',
          method: function () {
            let o = order.order_no.toString() + '222'
            wx.ready(() => {
              tenPayInWechat(o, order.mall_name)
                .then(datas => {
                  if (datas.state === 0) return self.$toast(datas.errormsg)
                  let data = datas.data
                  function onBridgeReady(){
                    WeixinJSBridge.invoke(
                      'getBrandWCPayRequest', {
                        "appId": data.appId,
                        "timeStamp": String(data.timeStamp),
                        "nonceStr": data.nonceStr,
                        "package": data.package,
                        "signType": data.signType,
                        "paySign": data.paySign
                      },
                      function(res){
                        if(res.err_msg == "get_brand_wcpay_request:ok" ) {
                          alert('支付成功')
                          self.$router.replace('/order')
                        }
                      }
                    );
                  }
                  if (typeof WeixinJSBridge == "undefined"){
                    if( document.addEventListener ){
                      document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
                    }else if (document.attachEvent){
                      document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
                      document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
                    }
                  }else{
                    onBridgeReady();
                  }
                }).catch(err => alert('出错了' + err))
            })
          }
        }
      ])
    })
  },
  methods: {
    getDatas () {
      getOrderList()
        .then(data => {
          if (data.state) {
            this.$set(this, 'orderList', data.data.list)
          }
        })
        .catch(err => console.log(err))
    }
  },
  components: {
    AppHeader,
    OrderContent
  }
}
</script>

<style lang="less" scoped>
  .order-wrap{
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    box-sizing: border-box;
    .mint-navbar{
      border-bottom: 1px solid #ddd;
    }
  }
</style>



