<template>
  <div>
    <OrderItem
      v-for="(item, index) in order"
      :key="index"
      :orderInfo="item"
    ></OrderItem>
  </div>
</template>

<script>
import OrderItem from './order-item.vue'
export default {
  name: 'Order-All',
  props: {
    order: Array
  },
  components: {
    OrderItem
  }
}
</script>

<style lang="less" scoped></style>



